body {
    font-family: Georgia, 'Times New Roman', Times, serif;
    margin: 0;
    padding: 0;
    background-image: url('./img/vivid-blurred-colorful-background_58702-2563.avif');
    background-size: cover;
}

.breed-select-container {
    display: none;
}

h1 {
    text-align: center;
    padding: 20px 0;
}

img {
    width: 350px;
}

.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
}

.select-container {
    margin-bottom: 20px;
}

.loader {
    display: none;
    text-align: center;
    margin-bottom: 20px;
}

.error {
    display: none;
    text-align: center;
    color: red;
    margin-bottom: 20px;
}

.cat-info {
    display: none;
    text-align: center;
}

.loader-spinner {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: 0 auto;
    margin-bottom: 10px;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}